/* Carousel and pictures */
.view:not(.hm-zoom) {
  overflow: visible; }

/* Margin from the top */
.container .row {
  margin-top: 2.5rem; }

/* Simple blog listing */
.simple-blog-listing .single-post {
  margin-bottom: 5rem; }
  .simple-blog-listing .single-post.simple-post-text {
    margin-bottom: 0;
    border-bottom: 1px solid #e0e0e0; }
    .simple-blog-listing .single-post.simple-post-text .post-pictures img {
      margin-bottom: 1.5rem; }
  .simple-blog-listing .single-post h5 {
    text-align: center;
    margin-bottom: 1.5rem; }
  .simple-blog-listing .single-post h2 {
    text-align: center;
    margin-top: 2rem;
    margin-bottom: 1.2rem; }
  .simple-blog-listing .single-post .post-data h5 {
    margin-bottom: 2rem;
    font-weight: 400; }
    .simple-blog-listing .single-post .post-data h5 i {
      padding-left: 1rem; }

/* Archive */
.archive ul li {
  padding-top: 0.8rem;
  border-top: 1px solid #eceeef;
  padding-bottom: 0.3rem; }
  .archive ul li:first-of-type {
    border-top: none; }

/* Author widget */
.simple-author-widget .author-photo {
  margin-bottom: 1.8rem; }

.simple-author-widget .about-author h4 {
  text-align: center;
  margin-bottom: 1.5rem; }

.simple-author-widget .about-author p {
  text-align: justify; }

/* Simple author page */
.simple-author-page h1 {
  margin-bottom: 2rem; }

.simple-author-page p {
  text-align: center; }

.simple-author-page .view img {
  margin-top: 2rem; }
